.chatRoomContailter {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.userList {
    flex: 2;
    height: 100%;
    border: 1px solid #999;
}

.chatRightList {
    flex: 8;
    height: 100%;
}

.chatContent {
    overflow: auto;
    height: 90%;
    padding: 20px;
}

.chatLeft {
    display: flex;
    align-items: center;
}

.chatLeft img {
    margin-right: 10px;
    width: 60px;
}

.chatLeft .leftText {
    display: flex;
    flex-direction: column;
}

.chatLeft .leftText .leftTitle {
    font-size: 20px;
    color: #999;
}

.chatLeft .leftText .leftTx {
    background: #fff;
    font-size: 25px;
    border-radius: 5px;
    padding: 5px 20px;
    display: flex;
    align-items: center;
}

.chatRight {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    color: #fff;
}

.chatRight img {
    margin-left: 10px;
    width: 60px;
}

.chatRight .rightText {
    display: flex;
    flex-direction: column;
}

.chatRight .rightText .rightTitle {
    text-align: right;
    font-size: 20px;
    color: #999;
}

.chatRight .rightText .RightText {
    font-size: 25px;
    background: rgb(149, 236, 105);
    border-radius: 5px;
    padding: 5px 20px;
    display: flex;
    align-items: center;
}

.chatRoomFooter {
    height: 10%;
    background: #fff;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
}

.chatRoomFooter .chatSendButton {
    font-size: 25px;
    width: 150px;
    height: 100%;
}

.chatRoomFooter .chatSendContent {
    font-size: 25px;
    height: 100%;
}